@import 'reset';

:root{
    --color-red: #ED070F;
    --color-green: #0AB404;
    --color-white: white;
    --color-black: black;
    --color-blue: #44C1FD;
    --color-pink: #EC44FD;
}

html, body {
    height: 100%
}

* {
    box-sizing: border-box;
}


body {
    display: grid;
    grid-template-columns: 220px 1fr;
    grid-template-rows: 44px 1fr 44px;
    font-family: Arial, Helvetica, sans-serif;
}


header {
    grid-column: 1/3;
    grid-row: 1/2;
}

nav {
    grid-column: 1/2;
    grid-row: 2/3;
}

article {
    grid-column: 2/3;
    grid-row: 2/3;
}
header,
footer {
    background-color: var(--color-red);
}
footer {
    grid-column: 1/4;
    grid-row: 3/4;
}

nav {
    background-color: var(--color-green)
}